export default client => {  const { Menu } = client.ui.fragments;  return client.hoc({    classes: {      wrapper: `        position: relative;        height: 100vh;        width: 100%;      `,      menu: `        position: absolute;        top: 12px;        left: 12px;        cursor: pointer;        z-index: 10;        user-select: none;      `,      pages: `        position: absolute;        top: 0; left: 0; right: 0; bottom: 0;        overflow: hidden;      `,    },    sections: {      App: require('./pages/<App>/App.js').default,      Dashboard: require('./pages/<Dashboard>/Dashboard.js').default,    },    render({ classes, sections }) {      return client.html`        <div id='pages-wrapper' class=${classes.wrapper}>          <header id='menu' class=${classes.menu}>            ${Menu()}          </header>          <main id='pages' class=${classes.pages}>            ${sections.App()}            ${sections.Dashboard()}          </main>        </div>      `;    }  });};