export const Input = client => {  return client.hoc({    styles: {      input: `        font-size: 18px;        border: none;        outline: none;        padding: 20px;        background: hsl(0, 0%, 98%);        border-radius: 5px;        margin-bottom: 30px;      `,    },    actions(props, /* store */) {      return {        onKeyDown: e => {          // Tab pressed          if (e.keyCode === 9) {            e.preventDefault();            props.onClickRight();          }        },      };    },    render({ props, styles, actions }) {      const { type, placeholder, value, onInput } = props;      return client.html`        <input          type=${type}          placeholder=${placeholder}          value=${value}          style=${styles.input}          @input=${onInput}          @keydown=${actions.onKeyDown}        />      `;    }  });};