export default () => args => {  const { page, action } = args;  return {    steps: [      () => ({        domain: '_Store_',        method: 'set',        args: {          currentPage: page,        },        sideEffect: () => {          if (action === 'replace') window.history.replaceState(null, null, page);          if (action === 'push') window.history.pushState(null, null, page);          if (action === 'back') window.history.back();        },      }),    ],  };};