export default client => {  return client.hoc({    state(props, store) {      return {        currentPage: store.get('currentPage'),      };    },    styles: {      app: isVisible => `        display: flex;        position: absolute;        justify-content: center;        align-items: center;        min-width: 100%;        height: 100%;        transition: all .6s ease-out;        overflow: hidden;        background: hsl(240, 39%, 92%);        opacity: ${isVisible ? 1 : 0};        pointer-events: ${isVisible ? 'auto' : 'none'};      `,    },    render({ styles, state }) {      const { currentPage } = state;      return client.html`        <div id='app' style=${styles.app(currentPage === 'app')}>          I am the App!        </div>      `;    }  });};